import React, { Component } from 'react';

import Footer from '../Footer';
import NavbarFixed from '../NavbarFixed';
import Helmet from 'react-helmet'
import './index.css';

class App extends Component {
  render() {
    return (
      <div
        id="app"
        className="content"
      >
      <Helmet
        title="KTM Buddy Privacy Policy"
      >
      </Helmet>

        <NavbarFixed
          title="KTM Buddy - Privacy Policy"
        />
          <div style={{ padding: '20px'}}>
          <p className="c0" id="h.gjdgxs"><span className="c2 c5 c6">Privacy Policy</span></p>
          <p className="c0"><span className="c5 c7">General</span></p>
          <p className="c0"><span className="c3">Respecting and protecting our customers&rsquo; privacy and personal data is important
              to the KTMBuddy (</span><span className="c5">&ldquo;we&rdquo;</span><span className="c3">&nbsp;or&nbsp;</span><span className="c5">&ldquo;us&rdquo;</span><span
              className="c3">). This Privacy Policy will help you to understand how we collect, use and safeguard your personal
              data in our interactions with you.&nbsp;<br/><br/>Throughout this Privacy Policy, the term &quot;personal
              data&quot; means any data relating directly or indirectly to you, from which it is practicable for your identity
              to be directly or indirectly ascertained, and in a form in which access to or processing of the data is
              practicable.&nbsp;<br/><br/>By visiting KTM Buddy, (the&nbsp;</span><span className="c5">&ldquo;Mobile App&rdquo;</span><span
              className="c1 c2">) or providing us with your personal data via other means, you agree that we will collect, use and
              store your personal data in accordance with this Privacy Policy, as amended from time to time. This Privacy
              Policy is incorporated into and forms part of our Terms and Conditions.&nbsp;<br/><br/>We regularly review this
              Privacy Policy and may from time to time revise it by updating this page. By continuing to use our services
              through the Mobile App after any amendments to this Privacy Policy, you are agreeing to this Privacy Policy as
              revised, so please visit this page periodically for updates.<br/></span></p>
          <p className="c0"><span className="c7 c5">Your Personal Information and How We Use It</span></p>
          <p className="c0"><span className="c1 c2">Collection of your Personal Data<br/><br/>We collect, use and store your personal
              data to ensure we can provide various services to you and offer you a personal experience online. This may occur
              on our Mobile App or via other means. We will keep your personal data confidential at all times and only collect,
              use and store it in accordance with this Privacy Policy.&nbsp;<br/><br/>Different types of personal data may be
              collected by us on our Mobile App.<br/></span></p>
          <p className="c0"><span className="c7 c5">Use of Your Personal Data</span></p>
          <p className="c0"><span className="c3">We use the personal data you provide to us for the following purposes (and any
              purposes directly related to these purposes) (collectively, the&nbsp;</span><span className="c5">&ldquo;Purposes&rdquo;</span><span
              className="c1 c2">):</span></p>
          <ul className="c9 lst-kix_list_1-0 start">
            <li className="c8"><span className="c1 c2">To provide our services to you (including assessing any application for and
                administration of such services). This includes responding to any questions you may have, assessing your
                applications with us for any of our services.</span></li>
            <li className="c8"><span className="c1 c2">To match your personal data with information you provided to us via other means;
                and</span></li>
            <li className="c8"><span className="c1 c2">To analyse trends and usage with us (whether on an individualised or aggregated
                basis), which helps us better understand how you and our collective customer base access and use our Mobile App
                and services, for the purposes of improving our services and responding to customer desires and preferences.
                For these purposes, we will use personal data about your gender, age, interests, geographical location, site
                usage and other personal data.</span></li>
          </ul>
          <p className="c10"><span className="c1 c2"></span></p>
          <p className="c0"><span className="c7 c5">Collection of Anonymous Data from You</span></p>
          <p className="c0"><span className="c1 c2">Separately, we may collect certain other data from you on our Mobile App:&nbsp;<br/><br/>We
              automatically collect data relating to your online session to provide our services to you, to gather statistical
              information on our server&rsquo;s usage and to help us better meet your demands and expectations.<br/><br/>We also
              use functionality that sets a unique identifier to track your use of our Mobile App across multiple devices so
              that your sessions are reported on our system as a single user rather than as multiple users.&nbsp;<br/></span></p>
          <p className="c0"><span className="c7 c5">Ads</span></p>
          <p className="c0"><span className="c1 c2">We will display ads from third party provider, including but not limited to Admob,
              Google AdSense. As a result of ads serving, cookies or ther technologies may be used to collect information.<br/></span></p>
          <p className="c0"><span className="c7 c5">Our Contact Details</span></p>
          <p className="c0"><span className="c1 c2">If you wish to contact us regarding this Privacy Policy (including in relation to
              any of your rights under this Privacy Policy or any other questions you may have), please contact our Privacy
              Officer via:&nbsp;</span></p>
          <ul className="c9 lst-kix_list_5-0 start">
            <li className="c11"><span className="c1">email, to </span><span className="c3">me@chinloongtan.com</span></li>
          </ul>
          <p className="c0 c13"><span className="c1 c2"></span></p>
          <p className="c4"><span className="c1 c2"></span></p>
          </div>
        <Footer />
      </div>
    );
  }
}

export default App;



